import { Component, OnInit,ViewChild } from '@angular/core';
import {  SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-artical',
  templateUrl: './artical.component.html',
  styleUrls: ['./artical.component.scss']
})
export class ArticalComponent implements OnInit {

  articalform:FormGroup;
  submitted:boolean;
  artical:any;
  articalName:any;
  metricsID:any;
  metricsName:any;
  volume:any;
  buyPrice:any;
  sellPrice:any;
  minSalQty:any;
  comment:any;
  buying:any;
  selling:any;
  articalobj:any;
  categoryName:any;
  _id:any;
  metrics:any;
  updatearticalform:FormGroup;
  updatecategoryName:any;
  updatearticalName:any;
  updatemetricsID:any;
  updatemetricsName:any;
  updatevolume:any;
  updatebuyPrice:any;
  updatesellPrice:any;
  updateminSalQty:any;
  updatecomment:any;
  updateSelling:boolean;
  updatebuying:boolean;
  @ViewChild('closebutton') closebutton;


  constructor( public suppler:SupplerService,
    private formBuilder: FormBuilder,
    private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {
    this.buying = false;
    this.selling = false;
    this.updateSelling = false;
    this.updateSelling = false;
    this.LoadArticalCategory();
    this.validator();
    this.viewArtical();
    this.updateValidator();
    this.LoadMetrics();
  }

   //get value from
   get f(){
    return this.articalform.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.articalform.reset();
  }

  //validator
  validator(){
  this.articalform = this.formBuilder.group({
    categoryName:["",Validators.required],
    articalName:["",Validators.required],
    metricsID:[""],
    metricsName:["",Validators.required],
    volume:["",Validators.required],
    buyPrice:[Number,Validators.required],
    sellPrice:[Number,Validators.required],
    minSalQty:[Number,Validators.required],
    comment:[""],
    buying:[Boolean],
    selling:[Boolean],
  });
}

  //load Artical category
  LoadArticalCategory(){
    this.suppler.LoadArticalCategory().subscribe(
      (myArtical)=>{
        console.log(myArtical);
      this.artical = myArtical["data"];
      }
    );
  }

  //loadMetrics
  LoadMetrics(){
    this.suppler.LoadMetrics().subscribe(
      (myMetrics)=>{
        this.metrics = myMetrics["data"];
       // console.log(myMetrics);
 
      }
    );
  }

  metricsChange(val){
    console.log(val);
    
  }


  //add Artical 
  add(){

    
    this.submitted = true;
    if (this.articalform.invalid) {
           return;
       }


    const addArticalobj = {
      categoryName:this.categoryName,
      articalName:this.articalName,
      metricsID:this.metricsID,
      metricsName:this.metricsName,
      volume:this.volume,
      buyPrice:this.buyPrice,
      sellPrice:this.sellPrice,
      minSalQty:this.minSalQty,
      comment:this.comment,
      buying:this.buying,
      selling:this.selling
    }
    console.log(addArticalobj);
    
    this.suppler.AddArtical(addArticalobj).subscribe(
      (myArtical)=>{
        if(myArtical['status']){
          this._flashMessagesService.show(myArtical['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(myArtical['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.viewArtical();     
      },
      (err)=>{
        console.log(err);
      }

    );
  }


   //View Artical 
   viewArtical(){

    this.suppler.viewArtical().subscribe(
      (myArtical)=>{
          this.articalobj = myArtical["data"];
      },
      (err)=>{
        console.log(err);
      }
    );
  }


//Artical Delete
onDelete(_deleteID){
  var del = { ArticalID:_deleteID}
   
  this.suppler.ArticalDelete(del).subscribe(
    (delArtical)=>{
      console.log(delArtical);
      this._flashMessagesService.show(delArtical['msg'], { cssClass: 'alert-danger', timeout: 1200 });
      this.viewArtical();
    }
  );
}
// delete Artical again active
onActive(_ActiveID){
  var del = { ArticalID:_ActiveID}
   
  this.suppler.ArticalActive(del).subscribe(
    (delArtical)=>{
      console.log(delArtical);
      this._flashMessagesService.show(delArtical['msg'], { cssClass: 'alert-primary', timeout: 1200 });
      this.viewArtical();
    }
  );
}


//get value from
get up(){
  return this.updatearticalform.controls;
}
 
closeModel(){
  this.submitted = false;
  this.closebutton.nativeElement.click();
}
 //update validator
 updateValidator(){
  this.updatearticalform = this.formBuilder.group({
    updatecategoryName:["",Validators.required],
    updatemetricsName:["",Validators.required],
    updatearticalName:["",Validators.required],
    updatevolume:["",Validators.required],
    updatebuyPrice:[Number,Validators.required],
    updatesellPrice:[Number,Validators.required],
    updateminSalQty:[Number,Validators.required],
    updatebuying:[Boolean],
    updateSelling:[Boolean],
    updatecomment:[""]
  });
}

  //data bound to value
  bind(){
    this.updatecategoryName = this.categoryName;
    this.updatearticalName = this.articalName;
    this.updatemetricsName = this.metricsName;
    this.updatevolume = this.volume;
    this.updatebuyPrice = this.buyPrice;
    this.updatesellPrice = this.sellPrice;
    this.updateminSalQty = this.minSalQty;
    this.updatebuying = this.buying;
    this.updateSelling = this.selling;
    this.updatecomment = this.comment;
    this.unbind();
  }

  unbind(){
    this.categoryName = null;
    this.articalName = null;
    this.metricsName = null;
    this.volume = null;
    this.buyPrice = null;
    this.sellPrice = null;
    this.minSalQty = null;
    this.buying = null;
    this.selling = null;
    this.comment = null;
  }
  
//set  update model
Update(data){
   this._id         = data['_id'];
   this.categoryName  = data['categoryName'];
   this.articalName = data['articalName'];
   this.metricsName = data["metricsName"]
   this.volume      = data['volume'];
   this.buyPrice    = data['buyPrice'];
   this.sellPrice   = data['sellPrice'];
   this.minSalQty   = data['minSalQty'];
   this.buying      = data['buying'];
   this.selling     = data['selling'];
   this.comment     = data['comment'];
   this.bind();
}

// modal to data service
onUpdate(){


  this.submitted = true;
  if (this.updatearticalform.invalid) {
        return;
     }
     
  const data = {
    _id       :this._id,
    categoryName:this.updatecategoryName,
    articalName:this.updatearticalName,
    metricsName :this.updatemetricsName,
    volume    :this.updatevolume,
    minSalQty :this.updateminSalQty,
    buyPrice  :this.updatebuyPrice,
    sellPrice :this.updatesellPrice,
    buying    :this.updatebuying,
    selling   :this.updateSelling,
    comment   :this.updatecomment,
 
  }
 
   this.closeModel();
  this.suppler.UpdateArtical(data).subscribe(
    (res)=>{
      if(res['status']){
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
       }
      else{
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
       }
       
       this.resetForm();
       this.viewArtical(); 
    },
    (err)=>{
      console.log(err);
    }
  )
 }



}





