import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-suppler-invoice',
  templateUrl: './suppler-invoice.component.html',
  styleUrls: ['./suppler-invoice.component.scss']
})
export class SupplerInvoiceComponent implements OnInit {

  SupplerInvoice:FormGroup;
  submitted:boolean;
  articalCategoryname:any;
  articalCategory:any;
  articalname:any;
  artical:any;
  supplername:any;
  suppler:any;
  orderNumber:any;
  invoiceNumber:any;
  totalPrice:any;
  quantity:any;
  comment:any;
  buying:Boolean;
  selling:Boolean;
  obj:any;
  _id:any;
  updateSupplerInvoice:FormGroup;
  updatearticalCategoryname:any;
  updatearticalname:any;
  updatesupplername:any;
  updateorderNumber:any;
  updateinvoiceNumber:any;
  updatetotalPrice:any;
  updatequantity:any;
  updatecomment:any;
  updatebuying:any;
  updateselling:any;
  @ViewChild('closebutton') closebutton;


  constructor( public SupplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {

    this.buying = false;
    this.selling = false;
    this.updateselling = false;
    this.updatebuying = false;
    this.validator();
    this.view();
    this.LoadSuppler();
    this.LoadArtical();
    this.LoadArticalCategory();
    this.updateValidator();
    
  }

     //get value from
     get f(){
      return this.SupplerInvoice.controls;
    }
  
    // reset from
    resetForm(){
      this.submitted = false;   
      this.SupplerInvoice.reset();
    }
  
  
  
    //validator
    validator(){
    this.SupplerInvoice = this.formBuilder.group({
      articalCategoryname:["",Validators.required],
      articalname:["",Validators.required],
      supplername:["",Validators.required],
      orderNumber:["",Validators.required],
      invoiceNumber:["",Validators.required],
      totalPrice:["",Validators.required],
      quantity:["",Validators.required],
      comment:[""],
      buying:[Boolean],
      selling:[Boolean],
    });
  }


  //view
  view(){
    this.SupplerService.viewSupplerInvoice().subscribe(
      (res)=>{
        this.obj = res['data'];
        console.log(res);
      },
      (err)=>{
        console.log(err);
      }
    )
  }

  //laod Artical Category
  LoadArticalCategory(){
    this.SupplerService.LoadArticalCategory().subscribe(
      (res)=>{
        this.articalCategory = res["data"];
        console.log(this.articalCategory);
      });
  }

    //laod Artical 
    LoadArtical(){
      this.SupplerService.LoadArtical().subscribe(
        (res)=>{
          this.artical = res["data"];
          console.log(this.artical);
        });
    }

    //Load Suppler
    LoadSuppler(){
      this.SupplerService.LoadSuppler().subscribe(
        (res)=>{
          this.suppler = res["data"];
          console.log(this.suppler);
        }
      )
    }

  //add
  add(){

    this.submitted = true;
    if (this.SupplerInvoice.invalid) {
         return;
       }

    const _add = {
      articalCategoryname:this.articalCategoryname,
      articalname:this.articalname,
      supplername:this.supplername,
      orderNumber:this.orderNumber,
      invoiceNumber:this.invoiceNumber,
      totalPrice:this.totalPrice,
      quantity:this.quantity,
      comment:this.comment,
      buying:this.buying,
      selling:this.selling
    }
    this.SupplerService.AddSupplerInvoice(_add).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.view(); 
      },
      (err)=>{
        console.log(err);
      });
  }

//delete
  delete(_deleteID){
    const del = {_id:_deleteID};

    this.SupplerService.DeleteSupplerInvoice(del).subscribe(
      (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
      },
      (err)=>{
        console.log(err);
      });
  }

//Active
  Active(_activeID){
    const del = {_id:_activeID};

    this.SupplerService.ActiveSupplerInvoice(del).subscribe(
      (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
      },
      (err)=>{
        console.log(err);
      });
  }




 
//get value from
get up(){
  return this.updateSupplerInvoice.controls;
}
 
closeModel(){
  this.submitted = false;
  this.closebutton.nativeElement.click();
}
 //update validator
 updateValidator(){
  this.updateSupplerInvoice = this.formBuilder.group({
    updatearticalCategoryname:["",Validators.required],
    updatearticalname:["",Validators.required],
    updatesupplername:["",Validators.required],
    updateorderNumber:["",Validators.required],
    updateinvoiceNumber:["",Validators.required],
    updatetotalPrice:[Number,Validators.required],
    updatequantity:[Number,Validators.required],
    updatebuying:[Boolean],
    updateselling:[Boolean],
    updatecomment:[""],
  });
}
 

 //data bound to value
 bind(){
  this.updatearticalCategoryname = this.articalCategoryname;
  this.updatearticalname = this.articalname;
  this.updatesupplername = this.supplername;
  this.updateorderNumber = this.orderNumber;
  this.updateinvoiceNumber = this.invoiceNumber;
  this.updatetotalPrice = this.totalPrice;
  this.updatequantity = this.quantity;
  this.updatecomment = this.comment;
  this.updatebuying = this.buying;
  this.updateselling = this.selling;
  this.unbind();
} 

unbind(){
    this.articalCategoryname = null;
    this.articalname = null;
    this.supplername = null;
    this.orderNumber = null;
    this.invoiceNumber = null;
    this.totalPrice = null;
    this.quantity = null;
    this.comment = null;
    this.buying = null;
    this.selling = null;
} 
//set  update model
Update(data){
   this._id                 = data['_id'];
   this.articalCategoryname = data['articalCategoryname'];
   this.articalname         = data['articalname'];
   this.supplername         = data["supplername"]
   this.orderNumber         = data['orderNumber'];
   this.invoiceNumber       = data['invoiceNumber'];
   this.totalPrice          = data['totalPrice'];
   this.quantity            = data['quantity'];
   this.comment             = data['comment'];
   this.buying              = data['buying'];
   this.selling             = data['selling'];
   this.bind();
}

// modal to data service
onUpdate(){


  this.submitted = true;
  if (this.updateSupplerInvoice.invalid) {
        return;
     }

  const data = {
        _id                :this._id,
        articalCategoryname:this.updatearticalCategoryname,
        articalname        :this.updatearticalname,
        supplername        :this.updatesupplername,
        orderNumber        :this.updateorderNumber,
        invoiceNumber      :this.updateinvoiceNumber,
        totalPrice         :this.updatetotalPrice,
        quantity           :this.updatequantity,
        comment            :this.updatecomment,
        buying             :this.updatebuying,
        selling            :this.updateselling,
    
   }

  this.resetForm();
   this.closeModel();
  this.SupplerService.UpdateSupplerInvoice(data).subscribe(
    (res)=>{
      if(res['status']){
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
       }
      else{
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
       }
       
       this.resetForm();
       this.view(); 
    },
    (err)=>{
      console.log(err);
    }
  )
 }



}
