import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-vehicle',
  templateUrl: './vehicle.component.html',
  styleUrls: ['./vehicle.component.scss']
})
export class VehicleComponent implements OnInit {

  vehicle:FormGroup;
  comment:any;
  submitted:boolean;
  vehicleCategory:any;
  vehicleNumber:any;
  vehicledata:any;
  _id:any;
  updateVehicle:FormGroup;
  updateVehicleCategory:any;
  updateVehicleNumber:any;
  updateComment:any;
  @ViewChild('closebutton') closebutton;
 
  constructor(public supplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {
    this.validator();
    this.view();
    this.updateValidator();
  }

  
   //get value from
   get f(){
    return this.vehicle.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.vehicle.reset();
  }

  //validator
  validator(){
  this.vehicle = this.formBuilder.group({
    vehicleCategory:["",Validators.required],
    vehicleNumber:[""],
    comment:[""]

    });
   }

   //view
 view(){
  this.supplerService.viewvehical().subscribe(
    (res)=>{
      this.vehicledata = res['data'];
      console.log(res);
    },
    (err)=>{
      console.log(err);
    }
  )

 }
  
 //add
  add(){
    this.submitted = true;
    if (this.vehicle.invalid) {
       return;
      }
      const adddata = {
        vehicleCategory:this.vehicleCategory,
        vehicleNumber:this.vehicleNumber,
        comment:this.comment
      }
      this.supplerService.Addvehical(adddata).subscribe(
        (res)=>{
          if(res['status']){
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
           }
          else{
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
           }
           
          this.resetForm();
           this.view(); 
        },
        (err)=>{
          console.log(err);
        });
  }


  //delete
  onDelete(_deleteID){

    const del = {_id:_deleteID};

     this.supplerService.Deletevehical(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

  //Active
  onActive(_deleteID){
    const del = {_id:_deleteID};

     this.supplerService.Activevehical(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

  

   //get value from
   get up(){
    return this.updateVehicle.controls;
  }
   
  closeModel(){
    this.closebutton.nativeElement.click();
  }
   //update validator
   updateValidator(){
    this.updateVehicle = this.formBuilder.group({
      updateVehicleCategory:["",Validators.required],
      updateVehicleNumber:[""],
      updateComment:[""]
    });
  }

   //data bound to value
   bind(){
    this.updateVehicleCategory = this.vehicleCategory;
    this.updateVehicleNumber = this.vehicleNumber;
    this.updateComment = this.comment;
    this.unbind();
  }

  unbind(){
    this.vehicleCategory = null;
    this.vehicleNumber = null;
    this.comment = null;
  }

//set  update model
  Update(data){
     this.vehicleCategory = data['vehicleCategory'];
     this.vehicleNumber = data['vehicleNumber'];
     this.comment = data['comment'];
     this._id = data['_id'];
     this.bind();
  }

  //set the modal to data
  onUpdate(){
  
    this.submitted = true;
    if (this.updateVehicle.invalid) {
      return;
       }

    const data = {
      vehicleCategory :this.updateVehicleCategory,
      vehicleNumber:this.updateVehicleNumber,
      comment:this.updateComment,
       _id:this._id
    }
    
     this.closeModel();
    this.supplerService.Updatevehical(data).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.view(); 
      },
      (err)=>{
        console.log(err);
      }
    )
  }



}
