import { Component, OnInit } from '@angular/core';
import { UserService } from '../_services/user.service';
import { FlashMessagesService } from 'angular2-flash-messages';


@Component({
  selector: 'app-user-active',
  templateUrl: './user-active.component.html',
  styleUrls: ['./user-active.component.scss']
})
export class UserActiveComponent implements OnInit {

  constructor( private userService:UserService,
              private _flashMessagesService: FlashMessagesService) { }

        user:any;
        
  ngOnInit(): void {
      this.view();
  }

  view(){
    this.userService.viewUser().subscribe(
      (res)=>{
        this.user = res['data'];

        console.log(this.user);
      },
      (err)=>{
        console.log(err)
      }
    )
  }


  //delete
  delete(_deleteID){
    const del = {_id:_deleteID};

    this.userService.DeleteUser(del).subscribe(
      (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
      },
      (err)=>{
        console.log(err);
      });
  }

//Active
  Active(_activeID){
    const del = {_id:_activeID};

    this.userService.ActiveUser(del).subscribe(
      (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
      },
      (err)=>{
        console.log(err);
      });
  }

  
  //delete
  UserAccept(id){
    const accept = {_id:id};

    this.userService.AcceptUser(accept).subscribe(
      (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
      },
      (err)=>{
        console.log(err);
      });
  }

//Active
UserNonAccept(id){
    const nonaccept = {_id:id};

    this.userService.NonAcceptUser(nonaccept).subscribe(
      (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
      },
      (err)=>{
        console.log(err);
      });
  }


}
