import { Component, OnInit } from "@angular/core";
import { Validators, FormBuilder, FormGroup } from "@angular/forms";
import { Router, NavigationEnd } from "@angular/router";

import { RepService } from "../_services/rep.service";
import { HttpClient } from "@angular/common/http";

@Component({
  selector: "app-addshop",
  templateUrl: "./addshop.component.html",
  styleUrls: ["./addshop.component.scss"],
})
export class AddshopComponent implements OnInit {
  addShopForm: FormGroup;
  images: any;
  multipleImages = [];
  mySubscription: any;

  constructor(
    private http: HttpClient,
    private router: Router,
    private formBuilder: FormBuilder,
    private repservice: RepService
  ) {
    this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };
    this.mySubscription = this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        // Trick the Router into believing it's last link wasn't previously loaded
        this.router.navigated = false;
      }
    });
  }

  ngOnDestroy() {
    if (this.mySubscription) {
      this.mySubscription.unsubscribe();
    }
  }

  ngOnInit(): void {
    this.addShopForm = this.formBuilder.group({
      shopName: ["", Validators.required],
      ownerName: ["", Validators.required],
      address: ["", Validators.required],
      city: ["", Validators.required],
      mobile: ["", Validators.required],
      business: ["", Validators.required],
    });
  }

  selectImage(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.multipleImages.push(file);
    }
  }

  selectImage1(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.multipleImages.push(file);
    }
  }

  onMultipleSubmit() {
    const formData = new FormData();
    for (let img of this.multipleImages) {
      formData.append("files", img);
    }

    this.http.post<any>("http://localhost:3000/shops/file", formData).subscribe(
      (res) => console.log(res),
      (err) => console.log(err)
    );
  }

  addShop() {
    this.repservice.registerShop(this.addShopForm.value).subscribe(
      (data) => {
        this.onMultipleSubmit();
        console.log(data);
        this.addShopForm.reset();
        this.router.navigateByUrl("/addShop");
      },
      (err) => {
        console.log(err);
      }
    );
  }
}
