import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { RepHomeComponent } from './rep-home/rep-home.component';
import { ManagerHomeComponent } from './manager-home/manager-home.component';
import { AuthGuard } from './_helpers/auth.guard';

import { ArticalCategoryComponent } from './artical-category/artical-category.component';
import { MetricsComponent } from './metrics/metrics.component';
import { ArticalComponent } from './artical/artical.component';
import { ProductComponent } from './product/product.component';
import { SupplerComponent } from './suppler/suppler.component';
import {  StockWarehouseComponent } from './stock-warehouse/stock-warehouse.component';
import { SupplerInvoiceComponent } from './suppler-invoice/suppler-invoice.component';
import { AddshopComponent } from './addshop/addshop.component';
import { RepOrdersComponent } from './rep-orders/rep-orders.component';
import { UserActiveComponent} from './user-active/user-active.component';
import {  ExpencesComponent } from './expences/expences.component';
import {  ExpencesTypeComponent } from './expences-type/expences-type.component';
import { TransportExpenceTypeComponent } from './transport-expence-type/transport-expence-type.component';
import { TransportExpenceComponent } from './transport-expence/transport-expence.component';
import { VehicleComponent } from './vehicle/vehicle.component';
import { SalesDayStaticComponent } from './sales-day-static/sales-day-static.component' ;
import { SalesMonthStaticComponent } from './sales-month-static/sales-month-static.component';

const routes: Routes = [
  {
    path:'rep-home',
    component: RepHomeComponent,
    canActivate:[AuthGuard]
  },
  {
    path:'manager-home',
    component: ManagerHomeComponent,
    canActivate:[AuthGuard]
  },
  {
    path:'artical-category',
    component:ArticalCategoryComponent,
    canActivate:[AuthGuard]
  },
  {
    path:'artical',
    component:ArticalComponent,
    canActivate:[AuthGuard]
  },
  {
    path:'metrics',
    component:MetricsComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'product',
    component:ProductComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'suppler',
    component:SupplerComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'supplerInvoice',
    component:SupplerInvoiceComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'activeUser',
    component:UserActiveComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'Expences',
    component:ExpencesComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'ExpencesType',
    component:ExpencesTypeComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'TransportExpencesType',
    component:TransportExpenceTypeComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'vehicle',
    component:VehicleComponent,
    canActivate:[AuthGuard]
  },
  {
    path:'TransportExpences',
    component:TransportExpenceComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'stockWarehouse',
    component:StockWarehouseComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'salesdaystatic',
    component:SalesDayStaticComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'salesmonthstatic',
    component:SalesMonthStaticComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'addshop',
    component:AddshopComponent,
    canActivate:[AuthGuard]

  },
  {
    path:'rep-orders',
    component: RepOrdersComponent,
    canActivate:[AuthGuard]
  },
  {
    path:'',
    component: LoginComponent
  },
  {
    path:'register',
    component: RegisterComponent
  },
  { path: '**', redirectTo: ''}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
