import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-expences-type',
  templateUrl: './expences-type.component.html',
  styleUrls: ['./expences-type.component.scss']
})
export class ExpencesTypeComponent implements OnInit {
 
  expences:FormGroup;
  submitted:boolean;
  ExpencesType:any;
  exprencedata:any;
  _id:any;
  updateexpences:FormGroup;
  updateExpencesType:any;
  @ViewChild('closebutton') closebutton;

 

  constructor(public supplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }   


  ngOnInit(): void {
    this.validator();
    this.view();
    this.updateValidator();
  }

  
   //get value from
   get f(){
    return this.expences.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.expences.reset();
  }

  //validator
  validator(){
  this.expences = this.formBuilder.group({
      ExpencesType:["",Validators.required],
    });
   }

   //view
 view(){
  this.supplerService.viewExpenceType().subscribe(
    (res)=>{
      this.exprencedata = res['data'];
      console.log(res);
    },
    (err)=>{
      console.log(err);
    }
  )

 }
  
 //add
  add(){
    this.submitted = true;
    if (this.expences.invalid) {
       return;
      }
      const adddata = {
        ExpencesType:this.ExpencesType
      }
      this.supplerService.AddExpenceType(adddata).subscribe(
        (res)=>{
          if(res['status']){
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
           }
          else{
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
           }
           
          this.resetForm();
           this.view(); 
        },
        (err)=>{
          console.log(err);
        });
  }


  //delete
  onDelete(_deleteID){

    const del = {_id:_deleteID};

     this.supplerService.DeleteExpenceType(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

  //Active
  onActive(_deleteID){
    const del = {_id:_deleteID};

     this.supplerService.ActiveExpenceType(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

   //get value from
   get up(){
    return this.updateexpences.controls;
  }
   
  closeModel(){
    this.closebutton.nativeElement.click();
  }
   //update validator
   updateValidator(){
    this.updateexpences = this.formBuilder.group({
      updateExpencesType:["",Validators.required]
    });
  }


  //data bound to value
  bind(){
    this.updateExpencesType = this.ExpencesType;
    this.unbind();
  }
  unbind(){
    this.ExpencesType = null;
  }


//set  update model
  Update(data){
     this.ExpencesType = data['ExpencesType'];
     this._id = data['_id'];
     this.bind();
  
  }

  //set the modal to data
  onUpdate(){
  
    this.submitted = true;
    if (this.updateexpences.invalid) {
      return;
       }

    const data = {
       ExpencesType :this.updateExpencesType,
       _id:this._id
    }
    
     this.closeModel();
    this.supplerService.UpdateExpenceType(data).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.view(); 
      },
      (err)=>{
        console.log(err);
      }
    )
  }

}
