import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-artical-category',
  templateUrl: './artical-category.component.html',
  styleUrls: ['./artical-category.component.scss']
})
export class ArticalCategoryComponent implements OnInit {

  articalCategoryform:FormGroup;
  updateArticalCategoryform:FormGroup;
  submitted:boolean;
  categoryName:any;
  metrics:any;
  metricsID:any;
  metricsName:any;
  buying:boolean;
  selling:boolean;
  comment:any;
  articalobj:any;
  updatemetricsID:any;
  updatemetricsName:any;
  updatebuying:boolean;
  updateArticalCategoryname:any;
  productModalName:any;
  updatecomment:any;
  _id:any;
  updateSelling:boolean;
  @ViewChild('closebutton') closebutton;


  constructor( public supplerService:SupplerService,
    private formBuilder: FormBuilder,
    private _flashMessagesService: FlashMessagesService) { }



  ngOnInit(): void {
    this.buying = false;
    this.selling = false;
    this.updatebuying = false;
    this.updateSelling = false;
    this.LoadMetrics();
    this.viewArticalCategory();
    this.validator();
    this.updateValidator();
  }

   //get value from
   get f(){
    return this.articalCategoryform.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.articalCategoryform.reset();
  }



  //validator
  validator(){
  this.articalCategoryform = this.formBuilder.group({
    categoryName:["",Validators.required],
    metricsName:["",Validators.required],
    comment:[""],
    buying:[Boolean],
    selling:[Boolean],
  });
}

 //loadMetrics
  LoadMetrics(){
    this.supplerService.LoadMetrics().subscribe(
      (myMetrics)=>{
        this.metrics = myMetrics["data"];
        console.log(myMetrics);
 
      }
    );
  }

//add Artical Category
  AddArticalCategory(){

    this.submitted = true;
    if (this.articalCategoryform.invalid) {
      return;
       }
   
    const artical = {
      categoryName:this.categoryName,
      metricsName:this.metricsName,
      buying:this.buying,
      selling:this.selling,
      comment:this.comment
    } 
   
     this.supplerService.AddArticalCategory(artical).subscribe(
        (artical)=>{
          if(artical['status']){
            this._flashMessagesService.show(artical['msg'], { cssClass: 'alert-success', timeout: 1200 });
           }
          else{
            this._flashMessagesService.show(artical['msg'], { cssClass: 'alert-warning', timeout: 1200 });
           }
           
          this.resetForm();
           this.viewArticalCategory();       
        },
        (err)=>{
         console.log(err);
        }
      );
  }

   //View Artical Category
   viewArticalCategory(){

    this.supplerService.viewArticalCategory().subscribe(
      (myArtical)=>{
          this.articalobj = myArtical["data"];
          console.log(this.articalobj);
      },
      (err)=>{
        console.log(err);
      }
    );
  }

  
//Artical Delete
onDelete(_deleteID){
  var del = { ArticalID:_deleteID}
   
  this.supplerService.onArticalCategoryDelete(del).subscribe(
    (delArtical)=>{
      console.log(delArtical);
      this._flashMessagesService.show(delArtical['msg'], { cssClass: 'alert-danger', timeout: 1200 });
      this.viewArticalCategory();
    }
  );
}
// delete Artical again active
onActive(_ActiveID){
  var del = { ArticalID:_ActiveID}
   
  this.supplerService.onArticalCategoryActive(del).subscribe(
    (delArtical)=>{
      console.log(delArtical);
      this._flashMessagesService.show(delArtical['msg'], { cssClass: 'alert-primary', timeout: 1200 });
      this.viewArticalCategory();
    }
  );
}


//get value from
get up(){
  return this.updateArticalCategoryform.controls;
}
 
closeModel(){
  this.submitted = false;
  this.closebutton.nativeElement.click();
}
 //update validator
 updateValidator(){
  this.updateArticalCategoryform = this.formBuilder.group({
    updateArticalCategoryname:["",Validators.required],
    updatemetricsName:["",Validators.required],
    updatebuying:[Boolean],
    updateSelling:[Boolean],
    updatecomment:[""]
  });
}


  //data bound to value
  bind(){
    this.updateArticalCategoryname = this.categoryName;
    this.updatemetricsName = this.metricsName;
    this.updatebuying = this.buying;
    this.updateSelling = this.selling;
    this.updatecomment = this.comment;
    this.unbind();
  }

  unbind(){
     this.categoryName = null;
     this.metricsName = null;
     this.buying = null;
     this.selling = null;
     this.comment = null;
  }
//set  update model
Update(data){
   this.categoryName = data['categoryName'];
   this.metricsName = data['metricsName']
   this._id = data['_id'];
   this.buying = data['buying'];
   this.selling = data['selling'];
   this.comment = data['comment'];
   this.bind();
}

// modal to data service
onUpdate(){

  this.submitted = true;
  if (this.updateArticalCategoryform.invalid) {
    return;
     }

  const data = {
    _id:this._id,
    categoryName:this.updateArticalCategoryname,
    metricsName:this.updatemetricsName,
    buying:this.updatebuying,
    selling:this.updateSelling,
    comment:this.updatecomment
 
  }

   this.closeModel();
  this.supplerService.UpdateArticalCategory(data).subscribe(
    (res)=>{
      if(res['status']){
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
       }
      else{
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
       }
       
       this.resetForm();
       this.viewArticalCategory(); 
    },
    (err)=>{
      console.log(err);
    }
  )
}



}
