import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';



@Component({
  selector: 'app-expences',
  templateUrl: './expences.component.html',
  styleUrls: ['./expences.component.scss']
})
export class ExpencesComponent implements OnInit {

  expences:FormGroup;
  submitted:boolean;
  ExpencesType:any;
  date:Date;
  cost:any;
  description:any;
  comment:any;
  exprencedata:any;
  _id:any;
  expenceTypedata:any;
  updateExpences:FormGroup;
  updateExpencesType:any;
  updateDate:Date;
  updateCost:any;
  updateDescription:any;
  updateComment:any;
  @ViewChild('closebutton') closebutton;

  constructor(public supplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {
    this.validator();
    this.view();
    this.LoadExpenceType();
    this.updateValidator();
  }

  
  
   //get value from
   get f(){
    return this.expences.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.expences.reset();
  }

  //validator
  validator(){
  this.expences = this.formBuilder.group({
      ExpencesType:["",Validators.required],
      date:[Date,Validators.required],
      cost:[Number,Validators.required],
      description:[""],
      comment:[""]
    });
   }

   //Load Expence Type
   LoadExpenceType(){
      this.supplerService.LoadExpenceType().subscribe(
        (res)=>{
          this.expenceTypedata = res["data"];
          console.log(this.expenceTypedata);
        }
      )
   }
   //view
 view(){
  this.supplerService.viewExpence().subscribe(
    (res)=>{
      this.exprencedata = res['data'];
      console.log(res);
    },
    (err)=>{
      console.log(err);
    }
  )

 }
  
 //add
  add(){
    this.submitted = true;
    if (this.expences.invalid) {
       return;
      }
      const adddata = {
        date:this.date,
        ExpencesType:this.ExpencesType,
        cost:this.cost,
        comment:this.comment,
        description:this.description
      }
      console.log(adddata);
      this.supplerService.AddExpence(adddata).subscribe(
        (res)=>{
          if(res['status']){
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
           }
          else{
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
           }
           
          this.resetForm();
           this.view(); 
        },
        (err)=>{
          console.log(err);
        });
  }


  //delete
  onDelete(_deleteID){

    const del = {_id:_deleteID};

     this.supplerService.DeleteExpence(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

  //Active
  onActive(_deleteID){
    const del = {_id:_deleteID};

     this.supplerService.ActiveExpence(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }


//get value from
get up(){
  return this.updateExpences.controls;
}
 
closeModel(){
  this.submitted = false;
  this.closebutton.nativeElement.click();
}
 //update validator
 updateValidator(){
  this.updateExpences = this.formBuilder.group({
    updateExpencesType:["",Validators.required],
    updateDate:[Date,Validators.required],
    updateCost:[Number,Validators.required],
    updateDescription:[""],
    updateComment:[""]
  });
}

//data bound to value
bind(){
  this.updateExpencesType = this.ExpencesType;
  this.updateDate = this.date;
  this.updateCost = this.cost;
  this.updateDescription = this.description;
  this.updateComment = this.comment;
  this.unbind();
}

unbind(){
  this.ExpencesType = null;
  this.date = null;
  this.cost = null;
  this.description = null;
  this.comment = null;
}

//set  update model
Update(data){
   this.ExpencesType = data['ExpencesType'];
   //this.date = data['date']
   this._id = data['_id'];
   this.cost = data['cost'];
   this.description = data['description'];
   this.comment = data['comment'];
   this.bind();
}

// modal to data service
onUpdate(){

  this.submitted = true;
  if (this.updateExpences.invalid) {
    return;
     }

  const data = {
    _id:this._id,
    ExpencesType:this.updateExpencesType,
    date:this.updateDate,
    cost:this.updateCost,
    description:this.updateDescription,
    comment:this.updateComment
 
  }
 console.log(data);
   this.closeModel();
  this.supplerService.UpdateExpence(data).subscribe(
    (res)=>{
      if(res['status']){
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
       }
      else{
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
       }
       
       this.resetForm();
       this.view(); 
    },
    (err)=>{
      console.log(err);
    }
  )
}




}
