import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-transport-expence-type',
  templateUrl: './transport-expence-type.component.html',
  styleUrls: ['./transport-expence-type.component.scss']
})
export class TransportExpenceTypeComponent implements OnInit {

  
  transportExpences:FormGroup;
  submitted:boolean;
  TransportExpencesType:any;
  exprencedata:any;
  _id:any;
  updateTransportExpences:FormGroup;
  updateTransportExpencesType:any;
  @ViewChild('closebutton') closebutton;

  constructor(public supplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {
    this.validator();
    this.view();
    this.updateValidator();
  }
  
  
   //get value from
   get f(){
    return this.transportExpences.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.transportExpences.reset();
  }

  //validator
  validator(){
  this.transportExpences = this.formBuilder.group({
    TransportExpencesType:["",Validators.required],
    });
   }

   //view
 view(){
  this.supplerService.viewTransportExpenceType().subscribe(
    (res)=>{
      this.exprencedata = res['data'];
      console.log(res);
    },
    (err)=>{
      console.log(err);
    }
  )

 }
  
 //add
  add(){
    this.submitted = true;
    if (this.transportExpences.invalid) {
       return;
      }
      const adddata = {
        TransportExpencesType:this.TransportExpencesType
      }
      this.supplerService.AddTransportExpenceType(adddata).subscribe(
        (res)=>{
          if(res['status']){
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
           }
          else{
            this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
           }
           
          this.resetForm();
           this.view(); 
        },
        (err)=>{
          console.log(err);
        });
  }


  //delete
  onDelete(_deleteID){

    const del = {_id:_deleteID};

     this.supplerService.DeleteTransportExpenceType(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

  //Active
  onActive(_deleteID){
    const del = {_id:_deleteID};

     this.supplerService.ActiveTransportExpenceType(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }


   //get value from
   get up(){
    return this.updateTransportExpences.controls;
  }
   
  closeModel(){
    this.closebutton.nativeElement.click();
  }
   //update validator
   updateValidator(){
    this.updateTransportExpences = this.formBuilder.group({
      updateTransportExpencesType:["",Validators.required]
    });
  }

   //data bound to value
   bind(){
    this.updateTransportExpencesType = this.TransportExpencesType;
    this.unbind();
  }

  unbind(){
    this.TransportExpencesType = null;
  }

//set  update model
  Update(data){
     this.TransportExpencesType = data['TransportExpencesType'];
     this._id = data['_id'];
     this.bind();
  }

  //set the modal to data
  onUpdate(){
  
    this.submitted = true;
    if (this.updateTransportExpences.invalid) {
      return;
       }

    const data = {
      TransportExpencesType :this.updateTransportExpencesType,
       _id:this._id
    }
    
     this.closeModel();
    this.supplerService.UpdateTransportExpenceType(data).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.view(); 
      },
      (err)=>{
        console.log(err);
      }
    )
  }

}
