import { Component, OnInit } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';
import { MatMonthView } from '@angular/material/datepicker';

@Component({
  selector: 'app-sales-month-static',
  templateUrl: './sales-month-static.component.html',
  styleUrls: ['./sales-month-static.component.scss']
})
export class SalesMonthStaticComponent implements OnInit {

  
  salesDayStatics:FormGroup;
  submitted:boolean;
  month:Date;
  articalCategory:any;
  artical:any;
  articalCategoryData:any;
  articalData:any;

  constructor(public supplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {
    this.validator();
    this.LoadArticalCategory();
    this.LoadArtical();
  }

  
  
   //get value from
   get f(){
    return this.salesDayStatics.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.salesDayStatics.reset();
  }

  //validator
  validator(){
  this.salesDayStatics = this.formBuilder.group({
      month:[Number,Validators.required],
      articalCategory:[Date,Validators.required],
      artical:["",Validators.required],
    });
   }

   LoadArticalCategory(){
     this.supplerService.LoadArticalCategory().subscribe(
       (res)=>{
         this.articalCategoryData = res['data'];
         
       })
   }

   LoadArtical(){
    this.supplerService.LoadArtical().subscribe(
      (res)=>{
        this.articalData = res['data'];
      })
  }


   find(){
    this.submitted = true;
    if (this.salesDayStatics.invalid) {
       return;
      }
       const findData = {
            month:this.month,
            articalCategory:this.articalCategory,
            artical:this.artical
       }
       console.log(findData);
   }

}
