import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {

  productfrom:FormGroup;
  submitted:boolean;
  productName:any;
  obj:any;
  _id:any;
  updateproductform:FormGroup;
  updateproductname:any;
  productModalName:any;
  @ViewChild('closebutton') closebutton;

  constructor(public supplerService:SupplerService,
    private formBuilder: FormBuilder,
    private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {
    this.view();
    this.validator();
    this.updateValidator();
  }

  get f(){
    return this.productfrom.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.productfrom.reset();
  }

  //validator
  validator(){
  this.productfrom = this.formBuilder.group({
    productName:["",Validators.required],
  });
}

  //view Product 
  view(){
    this.supplerService.viewProduct().subscribe(
       (data)=>{
        this.obj = data['data'];
        console.log(data);
       },
       (err)=>{
         console.log(err);
       }
    );
  }


  //add Product
  add(){
    
    this.submitted = true;
    if (this.productfrom.invalid) {
           return;
       }

    const addobj = {
      productName:this.productName
    }
    
    this.supplerService.AddProduct(addobj).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.view();  
      },
      (err)=>{
        console.log(err);
      });
  }

  //Product Delete
onDelete(_deleteID){
  var del = { _id:_deleteID}
   
  this.supplerService.ProductDelete(del).subscribe(
    (res)=>{
      console.log(res);
      this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
      this.view();
    }
  );
}
// delete Artical again active
onActive(_ActiveID){
  var del = { _id:_ActiveID}
   
  this.supplerService.ProductActive(del).subscribe(
    (res)=>{
      console.log(res);
      this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
      this.view();
    }
  );
}

  //get value from
  get up(){
    return this.updateproductform.controls;
  }
   
  closeModel(){
    this.closebutton.nativeElement.click();
  }
   //update validator
   updateValidator(){
    this.updateproductform = this.formBuilder.group({
      updateproductname:["",Validators.required]
    });
  }

  //data bound to value
  bind(){
    this.updateproductname = this.productModalName;
    this.unbind();
  }
  
  unbind(){
    this.productModalName = null;
  }
//set metrics update model
  Update(data){
     this.productModalName = data['productName'];
     this._id = data['_id'];
     this.bind();
  
  }

  //set the modal to data
  onUpdate(){
  
    this.submitted = true;
    if (this.updateproductform.invalid) {
      return;
       }

    const data = {
      productName:this.updateproductname,
       _id:this._id
    }
    
     this.closeModel();
    this.supplerService.UpdateProduct(data).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.view(); 
      },
      (err)=>{
        console.log(err);
      }
    )
  }


}
