import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-suppler',
  templateUrl: './suppler.component.html',
  styleUrls: ['./suppler.component.scss']
})
export class SupplerComponent implements OnInit {

  supplerform:FormGroup;
  submitted:boolean;
  productname:any;
  product:any;
  details:any;
  name:any;
  street:any;
  city:any;
  telephone:number;
  mobile:number;
  email:string;
  regNo:any;
  obj:any;
  _id:any;
 updatesupplerform:FormGroup;
 updateproductname:any;
 updatedetails:any;
 updatename:any;
 updatestreet:any;
 updatecity:any;
 updatetelephone:number;
 updatemobile:number;
 updateemail:string;
 updateregNo:any;
 @ViewChild('closebutton') closebutton;

  constructor(public supplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }      

  ngOnInit(): void {
    this.view();
    this.validator();
    this.loadProduct();
    this.updateValidator();
  }


  //Load Product
  loadProduct(){
    this.supplerService.ProductLoad().subscribe(
        (res)=>{
          this.product = res["data"];
          console.log(this.product);
        }
    );
  }
  
   //get value from
   get f(){
    return this.supplerform.controls;
  }

  // reset from
  resetForm(){
    this.submitted = false;   
    this.supplerform.reset();
  }

  //validator
  validator(){
  this.supplerform = this.formBuilder.group({
    productname:["",Validators.required],
    details:[""],
    name:["",Validators.required],
    street:["",Validators.required],
    city:["",Validators.required],
    telephone:["",Validators.required],
    mobile:["",Validators.required],
    email:["",Validators.required],
    regNo:["",Validators.required],
  });
}

  //view
  view(){
      this.supplerService.viewSuppler().subscribe(
        (res)=>{
          this.obj = res['data'];
          console.log(res);
        },
        (err)=>{
          console.log(err);
        }
      )
  }

  //add 
  add(){

      this.submitted = true;
      if (this.supplerform.invalid) {
        return;
        }

    const suppler = {
      productname:this.productname,
      details:this.details,
      name:this.name,
      street:this.street,
      city:this.city,
      telephone:this.telephone,
      mobile:this.mobile,
      email:this.email,
      regNo:this.regNo
    }
    console.log(suppler);

    this.supplerService.AddSuppler(suppler).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.view(); 
      },
      (err)=>{
        console.log(err);
      });
  }

  //delete
  delete(_deleteID){

    const del = {_id:_deleteID};

     this.supplerService.DeleteSuppler(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

  //Active
  Active(_deleteID){
    const del = {_id:_deleteID};

     this.supplerService.ActiveSuppler(del).subscribe(
       (res)=>{
        console.log(res);
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.view();
       },
       (err)=>{
         console.log(err);
       });
  }

 
//get value from
get up(){
  return this.updatesupplerform.controls;
}
 
closeModel(){
  this.submitted = false;
  this.closebutton.nativeElement.click();
}
 //update validator
 updateValidator(){
  this.updatesupplerform = this.formBuilder.group({
    updateproductname:["",Validators.required],
    updatename:["",Validators.required],
    updatestreet:["",Validators.required],
    updatecity:["",Validators.required],
    updatetelephone:["",Validators.required],
    updatemobile:["",Validators.required],
    updateemail:["",Validators.required],
    updateregNo:["",Validators.required],
    updatedetails:[""]
  });
}


  //data bound to value
  bind(){
    this.updateproductname = this.productname;
    this.updatename = this.name;
    this.updatestreet = this.street;
    this.updatecity = this.city;
    this.updatetelephone = this.telephone;
    this.updatemobile = this.mobile;
    this.updateemail = this.email;
    this.updateregNo = this.regNo;
    this.updatedetails = this.details;
    this.unbind();
  }

  unbind(){
     this.productname = null;
     this.name = null;
     this.street = null;
     this.city = null;
     this.telephone = null;
     this.mobile = null;
     this.email = null;
     this.regNo = null;
     this.details = null;

  }
//set  update model
Update(data){
   this._id         = data['_id'];
   this.productname = data['productname'];
   this.name        = data['name'];
   this.street      = data["street"]
   this.city        = data['city'];
   this.telephone   = data['telephone'];
   this.mobile      = data['mobile'];
   this.email       = data['email'];
   this.regNo       = data['regNo'];
   this.details     = data['details'];
   this.bind();
}

// modal to data service
onUpdate(){


  this.submitted = true;
  if (this.updatesupplerform.invalid) {
        return;
     }
     
  const data = {
    _id        :this._id,
    productname:this.updateproductname,
    name       :this.updatename,
    street     :this.updatestreet,
    city       :this.updatecity,
    telephone  :this.updatetelephone,
    mobile     :this.updatemobile,
    email      :this.updateemail,
    regNo      :this.updateregNo,
    details    :this.updatedetails,
 
  }

  this.resetForm();
   this.closeModel();
  this.supplerService.UpdateSuppler(data).subscribe(
    (res)=>{
      if(res['status']){
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
       }
      else{
        this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
       }
       
       this.resetForm();
       this.view(); 
    },
    (err)=>{
      console.log(err);
    }
  )
 }


}
