import { Component, OnInit,ViewChild } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';
import * as $ from 'jquery';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-metrics',
  templateUrl: './metrics.component.html',
  styleUrls: ['./metrics.component.scss']
})
export class MetricsComponent implements OnInit {

  metricsform:FormGroup;
  updatemetricsform:FormGroup;
  metricsName:any;
  metrics:any;
  metricsID:any;
  mersure:any;
  _id:any;
  submitted:boolean;
  updatemetricsname:any;
  @ViewChild('closebutton') closebutton;
  
  constructor(private supplerService:SupplerService,
              private formBuilder: FormBuilder,
              private _flashMessagesService: FlashMessagesService) { }

  ngOnInit(): void {
   this.validator();
    this.viewMetrics();
    this.updateValidator();
  }

  //get value from
  get f(){
    return this.metricsform.controls;
  }


  //validator
  validator(){
    this.metricsform = this.formBuilder.group({
      metricsName:["",Validators.required]
    });
  }

 
  // reset from
  resetForm(){
    this.submitted = false;   
    this.metricsform.reset();
  }

  //View Metrics
  viewMetrics(){
    this.supplerService.viewAllMetrics().subscribe(
      (myMetrics)=>{
          this.metrics = myMetrics["data"];
          console.log(this.metrics);
      },
      (err)=>{
        console.log(err);
      }
    );
  }

  //Metrics Add to DB
  AddMetrics(){

   this.submitted = true;
    if (this.metricsform.invalid) {
      return;
       }

    const add = {Measure:this.metricsName}
    this.supplerService.AddMetrics(add).subscribe(
      (myMetrics)=>{
        if(myMetrics['status']){
          this._flashMessagesService.show(myMetrics['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(myMetrics['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.viewMetrics();       
      },
      (err)=>{
       console.log(err);
      }
    );
  }
//Metrics Delete
  onDelete(_deleteID){
    var del = { metricsID:_deleteID}
     
    this.supplerService.onDelete(del).subscribe(
      (delMetrics)=>{
        console.log(delMetrics);
        this._flashMessagesService.show(delMetrics['msg'], { cssClass: 'alert-danger', timeout: 1200 });
        this.viewMetrics();
      }
    );
  }
// delete metrics again active
  onActive(_ActiveID){
    var del = { metricsID:_ActiveID}
     
    this.supplerService.onActive(del).subscribe(
      (delMetrics)=>{
        console.log(delMetrics);
        this._flashMessagesService.show(delMetrics['msg'], { cssClass: 'alert-primary', timeout: 1200 });
        this.viewMetrics();
      }
    );
  }

   //get value from
   get up(){
    return this.updatemetricsform.controls;
  }
   
  closeModel(){
    
    this.closebutton.nativeElement.click();
  }
   //update validator
   updateValidator(){
    this.updatemetricsform = this.formBuilder.group({
      updatemetricsname:["",Validators.required]
    });
  }

  //data bound to value
  bind(){
    this.updatemetricsname = this.mersure;
    this.unbind();
  }

  unbind(){
    this.mersure = null;
  }
//set metrics update model
  Update(data){
     this.mersure = data['Measure'];
     this._id = data['_id'];
      this.bind();
  }

  //set the modal to data
  onUpdate(){
  
    this.submitted = true;
    if (this.updatemetricsform.invalid) {
      return;
       }

    const data = {
       mersure:this.updatemetricsname,
       _id:this._id
    }
     this.closeModel();
    this.supplerService.onUpdateMetrics(data).subscribe(
      (res)=>{
        if(res['status']){
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-success', timeout: 1200 });
         }
        else{
          this._flashMessagesService.show(res['msg'], { cssClass: 'alert-warning', timeout: 1200 });
         }
         
        this.resetForm();
         this.viewMetrics(); 
      },
      (err)=>{
        console.log(err);
      }
    )
  }




}
